.lk-btn-yelow {
  height: 40px;
  border-radius: 3px;
  display: flex;
  width: 160px;
  text-align: center;
  justify-content: center;
  align-items: center;
  align-content: stretch;
  flex-wrap: nowrap;
  flex-direction: row;
  text-decoration: none;
  font-family: 'Trajan Pro 3';
  font-size: 14px;
  font-weight: 400;
  color: #0d0d0d;
  background: url(../img/Zheltaya.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all 0.5s ease;
  position: relative;
  span{
    position: relative;
  }
  @include hover{
    transition: all 0.5s ease !important;
    color: #ffffff !important;
    &::after{
     opacity: 1;
    }
    &::before{
      opacity: 1;
    }
  }
  @media (min-width:576px){
    &:active{
      background-image: url(../img/Chernaya-active.svg);
      span{
        color: #b3b3b3;
      }
    }
  }

  &::after{
    display: block;
    content: '';
    top: 2px;
    left: 0;
    width: 100%;
    height: 80%;
    box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.2);
    position: absolute;
    z-index: -1;
    opacity: 0;
    transition: all 0.5s ease;
  }
  &::before{
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    background-image: url(../img/Chernaya.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: all 0.5s ease;
  }
}

.lk-btn-black{
  height: 40px;
  border-radius: 3px;
  display: flex;
  width: 160px;
  text-align: center;
  justify-content: center;
  align-items: center;
  align-content: stretch;
  flex-wrap: nowrap;
  flex-direction: row;
  text-decoration: none;
