  font-size: 14px;
  font-weight: 400;
  color: white;
  background-image: url(../img/Chernaya.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all 0.5s ease !important;

  @include hover{
    background-image: url(../img/Chernaya.svg) !important;
