  }
}

.swiper-auto-height {
  .swiper-wrapper {
    align-items: stretch;
  }

  .swiper-slide {
    display: flex;
    flex-direction: column;
    height: auto;

    .swiper-slide-item {
      height: 100%;
    }
  }
}

.swiper-button-style {
  margin: 0;
  width: auto;
  height: auto;
  &::after {
    display: none;
  }
}

.swiper-button-next {
  transform: rotate(180deg);
}

.swiper-pagination.swiper-pagination-style {
  bottom: 0;
  .swiper-pagination-bullet {
    background-color: rgba(42, 42, 44, 0.3);
    width: 9px;
    height: 9px;
  }
  .swiper-pagination-bullet-active {
