.lk-entrance {
  margin-top: 30px;
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;
    max-width: 750px;
    margin: 0 auto;
    margin-bottom: 100px;
    @include small-tablet{
      flex-direction: column-reverse;
    }
  }

  &__entry {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mobile{
      width: 80%;
    }

  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    text-align: center;
    @include small-tablet{
      display: none;
    }
    &--mobile{
      display: none;
      @include small-tablet{
        display: block;
        margin-bottom: 17px;
        margin: 0 auto;
        max-width: 350px;
      }
    }
  }

  &__input {
    margin-bottom: 20px;
    @include small-tablet{
      max-width: 350px;
      width: 100%;
    }
  }

  &__submit {
  }

  &__img {
  }
}
