.lk-header{
  background-color: #f8f8f8;
  padding: 28px 0;
  @include small-tablet{
    background-color: #f4f4f4;
  }
  @include mobile{
    padding: 13px 0;
  }
  &__logo{
    display: flex;
    align-items: center;
    gap: 20px;
    @include mobile{
      width: 137px;
      height: 34px;
    }
  }

  &__logo-text{
    font-weight: 400;
    font-size: 14px;
    line-height: 129%;
    color: #c4c4c4;
    @include mobile{
      display: none;
    }
  }
}
  .lk-header__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

  }
.list-reset {
}
.logo {
}
.menu{
  position: absolute;
  background-color: white;
  top: calc(100% + 15px);
  right: 0;
  display: flex;
  align-items: center;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.03);
  padding: 25px;
  border-radius: 10px;
  opacity: 0;
  visibility: hidden;
  transition: all .2s ease-in;
  z-index: 7;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.03);
  @include mobile{
    position: fixed;
    // right: -100%;
    width: 100%;
    top: 0;
    flex-direction: column;
    padding: 30px;
    padding-top: 13px;
    height: 100%;
    align-items: stretch;
    border-radius: 0;
  }
  @include small-mobile{
    padding: 10px;
    padding-top: 13px;
  }
  &--active{
    opacity: 1;
    visibility: visible;
  }

  &__item {
    &:not(:last-child){
      padding-bottom: 15px;
      margin-bottom: 15px;
      border-bottom: 1px solid #e5e5e5;
      @include mobile{
        border: 0;
        padding: 0;
        margin-bottom: 20px;
      }
    }
  }
  &__link{
    font-weight: 400;
    font-size: 16px;
    color: #8a8a8a;
    transition: color .2s ease-in;
    &:hover{
      color: #2a2a2c;
    }
    @include mobile{
      color: black;
      font-size: 20px;
    }
  }
  &__list {
    display: flex;
    column-gap: 30px;
    flex-direction: column
  }
  &__logo{
    display: none;
    @include mobile{
      display: block;
      padding-bottom: 13px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.096);
      margin-bottom: 20px;
      img{
        width: 137px;
        height: 34px;
      }
    }
  }
}
.burger{
  @include burger;
  display: block;
  svg{
    @include mobile{
      width: 34px;
      height: 34px;
    }
  }
}
