.lk-footer {
    padding-bottom: 30px;
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 30px;
  }

  &__logo {
  }

  &__nav {
  }

  &__nav-list {
    display: flex;
    gap: 40px;
    row-gap: 30px;
    flex-wrap: wrap;
  }

  &__nav-item {
  }

  &__nav-link {
    font-weight: 400;
    font-size: 16px;
    color: #000;
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    gap: 72px;
    row-gap: 20px;
    @include mobile{
      flex-direction: column;
    }
  }

  &__link-item {
  }

  &__link {
    font-weight: 400;
    font-size: 12px;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #65646c;
  }

  &__coppyright {
    font-weight: 400;
    font-size: 12px;
    color: #65646c;
  }
}
