.lk-access {
  margin-top: 30px;
  &__table {
    border-radius: 20px;
    margin: 0 auto;
    margin-bottom: 100px;
    padding: 50px 30px 22px 30px;
    background-color: white;
    @include tablet{
      background-color: transparent;
      padding: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
  }

  &__content {
  }

  &__head {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-content: space-between;
    gap: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #000;
    @include tablet{
      display: none;
    }
  }

  &__head-grid-4{
    grid-template-columns: repeat(4, 1fr);
  }

  &__head-name {
    font-weight: 700;
    font-size: 15px;
    color: #2a2a2c;
    display: flex;
    align-items: center;
    gap: 8px;
    &--jstend{
      justify-self: end;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    @include tablet{
      gap: 30px;
    }
  }

  &__item {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;
    padding: 12px 0;
    align-items: center;
    &:not(:last-child){
      border-bottom: 1px solid #e5e5e5;
      @include tablet{
        border: 0;
      }
    }
    &:last-child{
      padding: 12px 0 0 0;
      @include tablet{
        padding: 30px;
      }
    }
    @include tablet{
      display: flex;
      flex-direction: column;
      gap: 0;
      border: 0;
      border-radius: 10px;
      background-color: white;
      padding: 30px;
    }
  }

  &__item-grid-4{
    grid-template-columns: repeat(4, 1fr);
  }

  &__item-info {
    font-weight: 400;
    font-size: 16px;
    color: #2a2a2c;
    @include tablet{
      display: flex;
      justify-content: space-between;
      gap: 15px;
      margin-bottom: 25px;
      width: 100%;
    }
  }

  &__item-info-name{
    display: none;
    @include tablet{
      display: block;
      font-weight: 400;
      font-size: 15px;
      color: #65646c;
    }
  }

  &__item-time {
    font-weight: 700;
    font-size: 16px;
  }

  &__item-time--green {
    color: #26d367;
  }

  &__item-time--red {
    color: #f10000;
  }

  &__item-link{
    justify-self: end;
    @include tablet{
      align-self: center;
    }
  }
}
