.burger {
  --burger-width: 39px;
  --burger-height: 39px;
  --burger-line-height: 1px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: block;
}
@media (max-width: 576px) {
  .burger {
    --burger-width: 34px;
    --burger-height: 34px;
  }
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  display: none;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
  display: none;
}
@media (max-width: 576px) {
  .burger--active {
    z-index: 8;
  }
}
@media (max-width: 576px) {
  .burger--active svg {
    display: none;
  }
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg) scale(0.6);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
@media (max-width: 576px) {
  .burger--active::before {
    display: block;
  }
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg) scale(0.6);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
@media (max-width: 576px) {
  .burger--active::after {
    display: block;
  }
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (max-width: 576px) {
  .burger svg {
    width: 34px;
    height: 34px;
  }
}

