/* stylelint-disable */

@mixin burger {
  --burger-width: 39px;
  --burger-height: 39px;
  --burger-line-height: 1px;
  @include mobile{
    --burger-width: 34px;
  --burger-height: 34px;
  }
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    display: none;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
    display: none;
  }

  &--active {
    @include mobile{
      z-index: 8;
    }
    svg{
      @include mobile{
        display: none;
      }
    }
    &::before {
      top: 50%;
      transform: rotate(45deg) scale(0.6);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;

      @include mobile{
        display: block;
      }

    }

    &::after {
      top: 50%;
      transform: rotate(-45deg) scale(0.6);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      @include mobile{
        display: block;
      }
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}
