.lk-panel {
  margin-top: 30px;
  margin-bottom: 100px;
  &__content {
    display: grid;
    grid-template-columns: 263px 263px 1fr;
    gap: 30px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  &__themes {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__themes-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__theme-item {
  }

  &__theme-link {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 153%;
    color: #65646c;
  }

  &__theme-link--active {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #000;
  }

  &__chat {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__chat-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__chat-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__chat-button {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 15px;
    color: #65646c;
  }

  &__chat-button--active {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #000;
  }

  &__chat-notification {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 10px;
    line-height: normal;
    color: #65646c;
    border-radius: 100%;
    background-color: #dff7ff;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  &__my-chat {
    padding-bottom: 14px;
  }

  &__my-chat-title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 128%;
    color: #000;
    margin-bottom: 30px;
  }

  &__course {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__course-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 15px;
    @include small-tablet{
      flex-direction: column;
      row-gap: 25px;
    }
  }

  &__course-info-item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
  }

  &__course-info-img {
  }

  &__course-info-content {
    display: flex;
    flex-direction: column;
    gap: 3px;
  }

  &__course-info-name {
    font-weight: 400;
    font-size: 15px;
    color: #65646c;
  }

  &__course-info-meaning {
    font-weight: 400;
    font-size: 16px;
    color: #000;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    span{
      color: #65646c;
    }
  }
}
