.lk-training {
  margin-top: 30px;
  margin-bottom: 100px;
  &__content {
    display: grid;
    grid-template-columns: 262px 1fr;
    gap: 30px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  &__sidebar {

  }

  &__accordion-list {
  }

  &__accordion-item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    padding: 20px 30px;
    svg{
      min-width: 20px;
      min-height: 20px;
    }
  }

  &__accordion-item--active {
    border-left: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
    background-color: #dff7ff;
  }

  &__main {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__main-info {
    border-radius: 20px;
    padding: 30px;
    background-color:  #fff;
  }

  &__title{
    @include mobile{
      text-align: center;
    }
  }

  &__main-title {
    margin: 0;
    margin-bottom: 23px;
  }

  &__main-video {
    border-radius: 20px;
    margin-bottom: 20px;
  }

  &__main-info-deskr {
    font-weight: 400;
    font-size: 14px;
    line-height: 129%;
    text-align: center;
    color: #65646c;
    margin: 0;
    margin-bottom: 20px;
    display: block;
  }

  &__main-estimate {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
  }

  &__main-rating {
    font-weight: 400;
    font-size: 17px;
    line-height: 106%;
    text-align: center;
    color: #c4c4c4;
  }

  &__main-rating-current {
    font-weight: 600;
    font-size: 25px;
    color: #fbd167;
  }

  &__main-estimate-btn{
    padding: 14px 30px;
  }

  &__main-banner {

  }

  &__main-banner-img {
    width: 100%;
    border-radius: 20px;
  }

  &__main-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 25px;
  }

  &__main-item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: #65646c;
    svg{
      min-width: 14px;
      min-height: 10px;
      max-width: 14px;
      max-height: 10px;
      margin-top: 4px;
    }
  }

  &__main-link {
    padding: 10px 40px;
    @include mobile{
      width: 100%;
      padding: 10px;
    }
  }

  &__main-link--download{
    margin-left: 24px;
    svg{
      path{
        fill: black;
      }
    }
    @include hover{
      svg{
        path{
          fill: white;
          stroke: unset;
        }
      }
    }
    @include mobile{
      margin-left: 0;
    }
  }

  &__main-links {
    display: flex;
    gap: 30px;
    row-gap: 25px;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__main-navigation {
    display: flex;
    justify-content: center;
    gap: 30px;
  }

  &__main-navigation-prev {
    padding: 13px 37px;
    @include mobile{
      padding: 13px;
      width: 100%;
      justify-content: center;
    }
  }

  &__main-navigation-next {
    padding: 13px 34px;
    @include mobile{
      padding: 13px;
      width: 100%;
    }
  }
}
