.lk {
  padding-top: 100px;
  margin: 0 30px;
  margin-bottom: 100px;
  @include mobile{
    padding-top: 60px;
    margin: 0;
    margin-bottom: 100px;
  }
  &__title {
    margin-bottom: 30px;
    @include mobile{
      text-align: center;
    }
  }

  &__content {
    max-width: 100%;
    margin: 0 auto;
    @include big-desktop{

    }
    .swiper{
      padding: 30px 250px;
      margin: -30px;
      @include big-desktop{
        padding: 30px 120px;
      }
      @include mobile{
        padding: 30px 0;
        margin: 0;
      }
    }

    .swiper-slide{
      max-width: 263px;
      @include mobile{
        width: auto !important;
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

      }
      .lk-swiper__slide{
        opacity: 0.3;
      }

      .lk-swiper__course-btn{

      }
      .lk-swiper__course-progress{

      }
    }

    .swiper-slide-visible{

      .lk-swiper__slide{
        opacity: 1;
      }

      .lk-swiper__course-btn{
        opacity: 1;
      }
      .lk-swiper__course-progress{
        opacity: 1;
      }

    }

  }

}
.container {
}
.lk-title-h1 {
}
.swiper {
}
.lk-swiper {
  &__slide {

  }

  &__course {
    background-size: cover;
    background-repeat: no-repeat;
    max-width: 183px;
    border-radius: 10px;
    padding: 30px 40px 25px 40px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    height: 345px !important;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @include tablet{
      margin: 0 auto;
    }
    @include mobile{

    }
  }

  &__course--open {
  }

  &__course-title {
    font-family: var(--second-family);
    font-weight: 300;
    font-size: 18px;
    line-height: 122%;
    text-align: center;
    color: #fff;
    text-align: center;
    margin: 0;
    margin-bottom: 20px;
  }

  &__course-btn {
    padding: 12px 18px;
    border-radius: 10px;
    background-color: #fff;
    transition: all .2s ease-in;
    width: 147px;
    display: flex;
    justify-content: center;
  }

  &__course-pagination{
    display: none;
    @include mobile{
      display: block;
    }
  }

  &__course-btn-text {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
  }

  &__course-img {
  }

  &__course-progress {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 139px;
    align-items: center;
    gap: 3px;
  }

  &__course-progress-percents {
    display: flex;
    align-items: center;
    gap: 3px;
    font-weight: 400;
    font-size: 12px;
    text-align: center;
    color: #2a2a2c;
  }

  &__course-progress-current {

  }

  &__course-progress-total {
    color: #8a8a8a;
  }

  &__course-progress-indicator {
    max-width: 139px;
    width: 100%;
    height: 7px;
    border-radius: 100px;
    position: relative;
    background-color: white;
    align-self: flex-start;
  }

  &__course-progress-bar{
    background-color: #26d367;
    height: 7px;
    border-radius: 100px;
  }

  &__course--close {
  }

  &__course-btn--close {
    background-color: #2a2a2c;
    color: white;
    &:hover{
      background-color: #26d367;
      .lk-swiper__course-btn-text{
        display: none;
      }
      .lk-swiper__course-btn-text--hover{
        display: flex;
      }
    }
  }

  &__course-btn-text--hover {
    display: none;
  }

  &__course-swiper-btns {
    width: 100%;
    max-width: 1140px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 70px;
    z-index: 3;
    @include mobile{
      display: none;
    }
    .swiper-button-prev{
      left: 0;
      top: -165px;
      @include big-desktop{
        left: 50px;
      }
      @include tablet{
        left: 50px;
      }
    }
    .swiper-button-next{
      right: 15px;
      top: -165px;
      @include big-desktop{
        right: 50px;
      }
      @include tablet{
        right: 50px;
      }
    }

  }
}
