}
.swiper-slide {
}
.btn-reset {
}
.swiper-button-prev {
}
.swiper-button-prev-course {
  svg{
    circle{
      transition: all .2s ease-in;
    }
    path{
      transition: all .2s ease-in;
    }
  }
  &:hover{
    svg{
      circle{
        fill: black;
      }
      path{
        fill: white;
      }
    }
  }
}
.swiper-button-next-course {
  svg{
    circle{
      transition: all .2s ease-in;
    }
    path{
      transition: all .2s ease-in;
    }
  }
  &:hover{
    svg{
      circle{
        fill: black;
      }
      path{
        fill: white;
      }
    }
  }
}
