.course-chat {
  margin-top: 30px;
  margin-bottom: 100px;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 30px;
    @include tablet{
      flex-direction: column;
    }
  }

  &__sidebar{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    @include tablet{
      width: 100%;
    }
  }

  &__bar {
    padding: 30px 30px 15px 30px;
    border-radius: 20px;
    background-color: white;
    width: 100%;
    max-width: 362px;
    @include tablet{
      max-width: 100%;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 128%;
    color: #000;
    margin: 0;
    margin-bottom: 16px;
  }

  &__list-wrapper{
    padding-right: 5px;
  }

  &__content-modal{
    .course-chat__message-text{
      margin-bottom: 12px;
    }

  }

  &__list {
    margin: 0 -30px;
    max-height: 666px;
    overflow: auto;
    @include mobile{
      max-height: 400px;
    }
    &::-webkit-scrollbar-track{
      border-radius: 10px;
      background-color: transparent;
    }
    &::-webkit-scrollbar{
      width: 4px;
      background-color: transparent;
    }
    &::-webkit-scrollbar-thumb{
      border-radius: 100px;
      background-color: #c4c4c4;
    }
  }

  &__item {
    padding: 16px 10px 16px 30px;
    cursor: pointer;
    &:not(:last-child){
      border-bottom: 1px solid #e5e5e5;
    }
    & + .course-chat__item--active{
      border-top: 0;
    }
  }

  &__item-info {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: flex-end;
    gap: 5px;
    margin-bottom: 2px;
  }

  &__item-name {
    font-weight: 400;
    font-size: 16px;
    color: #000;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__item-date {
    font-weight: 700;
    font-size: 10px;
    color: #a7a7a7;
    white-space: nowrap;
    width: 100%;
  }

  &__item-mess {
    font-weight: 400;
    font-size: 15px;
    line-height: 125%;
    color: #65646c;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__item--active {
    border: 1px solid #e5e5e5;
    background-color: #dff7ff;
  }

  &__item--notification{
    .course-chat__item-name{
      font-weight: 700;
    }
    .course-chat__item-mess{
      font-weight: 700;
    }
  }

  &__item-counter {
    font-weight: 700;
    font-size: 12px;
    color: #fff;
    border-radius: 100%;
    background-color: #26d367;
    min-width: 21px;
    min-height: 21px;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  &__item-check {
  }

  &__link {
    padding: 12px 35px;
    @include tablet{
      display: none;
    }
  }

  &__link--mobile{
    display: none;
    @include mobile{
      display: inline-block;
      margin: 0 auto;
    }
  }

  &__wrapper {
    border-radius: 20px;
    background-color: white;
    padding: 30px 5px 30px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include tablet{
      width: 100%;
    }
    @include mobile{
      padding: 0;
      background-color: unset;
    }
  }

  &__days-wrapper{

    @include mobile{
      border: 1px solid #c4c4c4;
      padding: 5px 5px 5px 0;
      border-radius: 10px;
      background-color: white;
    }
      &::-webkit-scrollbar-track{
        border-radius: 10px;
        background-color: transparent;
        margin-left: 10px;
        display: none;
      }
      &::-webkit-scrollbar{
        width: 4px;
        background-color: transparent;
        display: none;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 100px;
        background-color: #c4c4c4;
        display: none;
      }

  }

  &__days {
    display: flex;
    flex-direction: column;
    gap: 25px;
    overflow: auto;
    padding-right: 25px;
    height: 600px;
    @include mobile{
      padding-right: 5px;
      height: 460px;
      padding: 20px 5px 20px 10px;
    }
      &::-webkit-scrollbar-track{
        border-radius: 10px;
        background-color: transparent;
        margin-left: 10px
      }
      &::-webkit-scrollbar{
        width: 4px;
        background-color: transparent;
      }
      &::-webkit-scrollbar-thumb{
        border-radius: 100px;
        background-color: #c4c4c4;
      }


  }

  &__day {

  }

  &__date {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 5px;
    span{
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      text-align: center;
      color: #c4c4c4;
      box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.1);
      background: #fff;
      border-radius: 100px;
      padding: 5px 20px;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-right: 40px;
    @include mobile{
      margin-right: 20px;
    }
  }

  &__block--row{
    flex-direction: row;
    align-items: flex-start;
    gap: 15px;
    &.course-chat__block--right{
      align-items: flex-start;
      flex-direction: row-reverse;
      gap: 15px;
    }
  }

  &__content-modal{

  }

  &__avatar{
    min-width: 49px;
    min-height: 49px;
    max-width: 49px;
    max-height: 49px;
    @include mobile{
      display: none;
    }
  }

  &__user {
    font-weight: 400;
    font-size: 14px;
    color: #000;
  }

  &__message-text {
    border-radius: 10px;
    background-color: #ddffea;
    padding: 13px 30px 13px 13px;
    position: relative;

    p{
      font-weight: 400;
      font-size: 16px;
      line-height: 125%;
      color: #65646c;
      margin: 0;
      @include mobile{
        font-size: 14px;
      }
    }
  }

  &__addition {
    font-weight: 400;
    font-size: 10px;
    color: #65646c;

  }

  &__block--right {
    align-items: flex-end;
    margin: 0;
    margin-left: 40px;
    @include mobile{
      margin-left: 20px;
    }
    .course-chat__message-text{
      background-color: #dff7ff;
    }

  }

  &__message-status {
    position: absolute;
    right: 9px;
    bottom: 9px;
  }

  &__message {
    border-top: 1px solid #c4c4c4;
    padding: 30px 30px 0 30px;
    margin: 0 -5px 0 -30px;
    margin-top: 30px;
    @include mobile{
      border: 0;
      margin: 0;
      padding: 0;
      margin-top: 10px;

    }
  }

  &__message-form {
    border: 1px solid #c4c4c4;
    border-radius: 10px;
    padding: 14px 20px;
    @include mobile{
      border: 0;
      display: flex;
      justify-content: space-between;
      gap: 10px;
      padding: 0;
    }
  }

  &__message-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 33px;
    @include mobile{
    border: 1px solid #c4c4c4;
    border-radius: 10px;
    padding: 14px 14px 14px 10px;
    width: 85%;
    background-color: white;
    }
  }

  &__message-textarea {
    border: none;
    outline: transparent;
    width: 100%;
  }

  &__message-action {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__message-file {
  }

  &__message-file-input {
    display: none;
  }

  &__message-send {
    @include mobile{
      display: none;
    }
  }

  &__message-send--mobile{
    display: none;
    @include mobile{
      min-width: 54px;
      min-height: 54px;
      max-width: 54px;
      max-height: 54px;
      background-color: #26d367;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      svg{
        margin-left: 6px;
      }
      path{
        fill: white;
      }
    }
  }
}
